.wrapper {
    padding: 0 5rem;
    /*max-width: 80%;*/
    background: rgba(0, 0, 0, .5);

}

.header {
    display: flex;
    justify-content: space-between;
    border: 1px solid #fff;
}

.title {
    margin-left: 2rem;
    border: 1px solid #fff;
}

.nav, .nav > ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #fff;
    padding: 0;
}

.nav > ul li {
    font-size: 1.5rem;
    margin-left: 1rem;
    list-style-type: none;
}
@media screen and (max-width: 992px) {
    .nav, .nav > ul {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        border: 1px solid #fff;
        padding: 0;
    }
}
